.main-content {
  flex: 1 0 auto;
}

.c-main-navigation {
  display: flex;
  top: 1rem;
  right: 1rem;
  position: fixed;
  flex-grow: 6;
  justify-content: flex-end;
  flex-shrink: 0;
  z-index: 500;
}

.c-tb-content {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  height: 80px;
  margin: 0 auto;
}

.work-area {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
}

.company-contact {
  display: inline-block;
  position: fixed;
  padding-left: calc(100% - 460px) !important;
  padding-top: 10px !important;
}

.c-divider-main {
  border-bottom: 2px solid #0f72e5 !important;
  border-right: 2px solid #0f72e5 !important;
  margin: 5px;
}

.c-nav-item {
    display: inline-block;
    margin: 0 16px;
    position: relative;
    transition: color .2s linear,opacity .2s linear;
    font-size: 18px;
    font-weight: 600;
}

.c-nav-item::after {
  background: #0f72e5;
  bottom: 0;
  content: "";
  height: 2px;
  left: 0;
  position: absolute;
  -webkit-transform: scaleX(0);
  text-decoration: none;
  transform: scaleX(0);
  transition: background .25s,-webkit-transform .25s cubic-bezier(.55,.085,.68,.53);
  transition: background .25s,transform .25s cubic-bezier(.55,.085,.68,.53);
  transition: background .25s,transform .25s cubic-bezier(.55,.085,.68,.53),-webkit-transform .25s cubic-bezier(.55,.085,.68,.53);
  width: 100%;
}


