.c-about-section {
    transform: translateY(0px);
    will-change: transform;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: end;
    justify-content: flex-end;
    padding-top: 0;
    height: 100vh;
    color: #2f2f2f;
}

.c-about-section-top {
    display: block;
    overflow: hidden;
    opacity: 1;
    transform: translate(0px, 0px);
    position: absolute;
    top: calc((100vh - 25vw)/2);
    left: 0;
    width: 60vw;
    color: #2f2f2f;
}

.c-about-section-top::before {
    display: block;
    width: 100%;
    content: "";
    padding-top: 56.25%;
}

.c-about-section-top-imgParent {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #2f2f2f;
}

.c-about-section-img {
    display: block;
    position: relative;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    pointer-events: none;
    -webkit-transition: opacity 1s,-webkit-transform 1s;
    transition: opacity 1s,-webkit-transform 1s;
    transition: opacity 1s,transform 1s;
    transition: opacity 1s,transform 1s,-webkit-transform 1s;
    width: 100%;
    height: 100%;
}

.c-about-section-bottom {
    overflow: hidden;
    position: relative;
    margin-right: 8vw;
    margin-top: 10vw;
    width: 32vw;
    padding: 4.43vw 4.16vw;
}

.c-about-section-bottom-contact {
    overflow: hidden;
    position: relative;
    margin-right: 8vw;
    margin-top: 9vw;
    width: 32vw;
    padding: 2vw 2vw;
}

.c-about-bg-white {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background: #fff;
    width: 100%;
    height: 100%;
    color: #2f2f2f;
}

.c-about-section-text {
    width: 100%;
    border-left-width: 0;
    border-right-width: 0;
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-left-color: transparent;
    border-right-color: transparent;
    border-left-style: solid;
    border-right-style: solid;
    border-top: 0 solid transparent;
    border-bottom: 0 solid transparent;
    color: #2f2f2f;
}

.c-about-section-h3 {
    font-size: calc(1.5rem + (75vw - 36em)/72);
    line-height: 1.5;
    font-weight: 500;
    margin-bottom: .5em;
}

.c-about-section-p {
    margin-bottom: 0;
    opacity: .8;
    font-size: calc(.75rem + (56.25vw - 27em)/72);
    line-height: 1.81;
    font-weight: 400;
    color: #2f2f2f;
}